@import 'colors';

body {
  padding: 50px;
  background-color: #66ccff;
  font-family: sans-serif;
}

#container {
  display: flex;
  justify-content: center;
  flex-direction: column-reverse;
}
.todoListMain {
  .header {
    input {
      padding: 10px;
      font-size: 16px;
      border: 2px solid #fff;
      width: 165px;
    }

    button {
      padding: 10px;
      font-size: 16px;
      margin: 10px;
      margin-right: 0px;
      background-color: $blue;
      color: #fff;
      border: 2px solid #0066ff;
    }

    button:hover {
      background-color: #003399;
      border: 2px solid #003399;
      cursor: pointer;
    }
  }

  .theList {
    list-style: none;
    color: red;
    padding-left: 0;
    width: 250px;
  }

  .theList li {
    color: #333;
    background-color: rgba(255, 255, 255, 0.5);
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    list-style: none;
  }
}

ul.theList {
  padding: 0;
  list-style: none;
  display: inline-flex;
  justify-content: center;
  flex-direction: column-reverse;
}

.theList li {
  color: #333;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
  transition: background-color 0.2s ease-out;
}

.todoListMain .theList li:hover {
  background-color: pink;
  cursor: pointer;
}

ul.theList li:hover {
  background-color: red;
  color: #ffffff;
  cursor: pointer;
}
